import { useState } from 'react';
import { Link } from 'react-router-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useProfiles, { ProfileDefinitionVm } from '../../../hooks/profiles/useProfiles';
import ActionButton from '../../Buttons/ActionButton';
import ProfileDialog from '../../Dialog/ProfileDialog/ProfileDialog';
import Heart from '../../Icons/Heart/Heart';
import PageMeta from '../../Layout/PageMeta/PageMeta';
import LoadingParagraph from '../../Loaders/LoadingParagraph/LoadingParagraph';

const Profile = () => {
  const { data: profiles, isLoading } = useProfiles().fetchProfiles;
  const [isOpenCreate, setIsOpenCreate] = useState(false);

  return (
    <>
      <section>
        <PageMeta
          icon={Heart}
          title={t('My Profiles')}
          actions={
            <>
              <ActionButton icon="plus" onClick={() => setIsOpenCreate(true)} className="m-2">
                {t('Add Profile')}
              </ActionButton>
            </>
          }
        />
        {isLoading ? (
          <div className="-m-2 flex">
            <LoadingParagraph className="m-2 h-10 w-1/4" />
            <LoadingParagraph className="m-2 h-10 w-1/4" />
          </div>
        ) : (
          <div className="-m-2 flex flex-row items-stretch">
            {profiles.map((definition) => {
              return (
                <div className="w-1/4 p-2" key={definition.name}>
                  <ProfileDefinitionCard profileDef={definition} />
                </div>
              );
            })}
          </div>
        )}
      </section>
      <ProfileDialog
        isOpen={isOpenCreate}
        title={t('New Profile')}
        onCancel={() => {
          setIsOpenCreate(false);
        }}
        onConfirm={() => {
          setIsOpenCreate(false);
        }}
      />
    </>
  );
};

const ProfileDefinitionCard = ({ profileDef }: { profileDef: ProfileDefinitionVm }) => {
  return (
    <Link to={profileDef.slug} className="contents">
      <div className="h-full rounded-md border-[1px] border-gray-200 border-opacity-60 bg-white transition-colors hover:bg-slate-100 dark:border-gray-800 dark:bg-gray-800 dark:hover:bg-slate-700">
        <h2 className="bg-slate-100 p-4 text-xl dark:bg-slate-700 dark:text-slate-200">
          {profileDef.name}
        </h2>
        <div className="p-4">
          <p>{profileDef.description}</p>
        </div>
      </div>
    </Link>
  );
};

export default Profile;
